import axios from "axios";
import { useAuth0 } from "./auth0";
export const AccessControlLevel = {
    PUBLIC: "public",
    PROTECTED: "requires-authentication",
    RBAC: "requires-role-permission",
    CORS: "requires-cors-allowed-method",
};
const apiServerUrl = process.env.API_SERVER_URL;
const { getAccessToken } = useAuth0;
const makeRequest = async (options) => {
    try {
        const token = await getAccessToken();
        options.headers = Object.assign(Object.assign({}, options.headers), { "content-type": "application/json", Authorization: `Bearer ${token}` });
        const response = await axios(options);
        const { data } = response;
        console.log(`API sent: ${JSON.stringify(data)}`);
        return data;
    }
    catch (error) {
        if (axios.isAxiosError(error) && error.response) {
            throw new Error(error.response.data);
        }
        throw new Error(error.message);
    }
};
export const createAvailablePart = async (newPart) => {
    return makeRequest({
        url: `${apiServerUrl}/api/parts`,
        method: "POST",
        data: newPart,
    });
};
export const updateAvailablePart = async (id, part) => {
    return makeRequest({
        url: `${apiServerUrl}/api/parts/${id}`,
        method: "PUT",
        data: part,
    });
};
